/* Header.module.css */

.header {
	background: #fff;
	color: #fff;
	text-align: center;
	padding: 1rem;
}

nav {
	display: flex;
	justify-content: center;
}

ul {
	list-style: none;
	display: flex;
	gap: 1rem;
}

li {
	font-size: 1.2rem;
}

.navLink {
	color: #fff;
	text-decoration: none;
}
